.skill-card {
  width: max-content;
  padding: 8px 16px;
  height: 120px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 6px;
  margin-left: 60%;
  /* border-radius: 8px;
  background-color: aqua; */
}
.skill-icon {
  font-size: 80px;
}
.skill-name {
  font-size: 18px;
}
@media only screen and (max-width: 720px) {
  .skill-card {
    width: max-content;
    height: 100px;
    margin-left: 0%;
  }
  .skill-icon{
      font-size: 60px;
  }
}
